export default {
    data(){
        return{}
    },
    watch:{
        // RadarMapData(curVal,oldVal){
        //     this.drawPieRadarMap()
        // }
    },
    mounted(){
        this.drawPieRadarMap()
    },
    methods:{
        drawPieRadarMap(){
            let that = this
            let RadarMap = this.$echarts.init(this.$refs.echarts)
            RadarMap.setOption({
                // tooltip: {},
                legend: {
                    itemWidth: 10,
                    itemHeight: 10,
                    itemGap: 10,
                    x:'center',
                    y:'bottom',
                    data: ['no1', 'no2']
                },
                radar: {
                    name: {
                        textStyle: {color: '#313131'}
                    },
                    center: ['50%', '45%'], // 位置
                    radius: '60%', //大小
                    indicator: [
                        { name: '111', max: 100},
                        { name: '222', max: 100},
                        { name: '333', max: 100},
                        { name: '444', max: 100},
                        { name: '555', max: 100},
                        { name: '666', max: 100}
                    ],
                    splitArea: {show: false},
                    splitLine: {lineStyle: {color: '#ddd'}},
                    axisLine: {lineStyle: {color: '#ddd'}}
                },
                // grid:{
                //     // show:true,
                //     x:'20',
                //     y:'20',
                //     x2:'20',
                //     y2:'20',
                // },
                series: [{
                    name: '预算 vs 开销',
                    type: 'radar',
                    symbol: 'circle',
                    symbolSize: 8,  //--圆点大小
                    data : [
                        {
                            value : [40, 10, 28, 35, 50, 90],
                            name : 'no1',
                            itemStyle: {
                                normal: {
                                    color : "rgba(254, 153, 66, 1)",    // 图表中各个图区域的边框线拐点颜色
                                    lineStyle: {
                                        color:"rgba(254, 153, 66, 1)",  // 图表中各个图区域的边框线颜色
                                        width:1
                                    },
                                }
                            },
                            areaStyle: {
                                opacity: 0.6,                   // 图表中各个图区域的透明度
                                color: "rgba(254, 153, 66, 1)"  // 图表中各个图区域的颜色
                            }
                        },
                        {
                            value : [50, 40, 80, 31, 42, 20],
                            name : 'no2',
                            itemStyle: {
                                normal: {
                                    color : "rgba(64, 137, 255, 1)",            // 图表中各个图区域的边框线拐点颜色
                                    lineStyle: {
                                        color:"rgba(64, 137, 255, 1)",          // 图表中各个图区域的边框线颜色
                                        width:1
                                    }
                                }
                            },
                            areaStyle: {
                                opacity: 0.6,                           // 图表中各个图区域的透明度
                                color: "rgba(64, 137, 255, 1)"          // 图表中各个图区域的颜色
                            }
                        }
                    ]
                }]
            })
            window.addEventListener("resize", RadarMap.resize) // echarts提供自动缩放
        }
    }
}



